.Landing_btn__2GIZO {
  margin-left: 60px;
  margin-top: 60px;
  min-width: 300px;
  min-height: 60px;
  font-family: 'Nunito', sans-serif;
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  font-weight: 700;
  color: #313133;
  background: #4FD1C5;
  background: linear-gradient(90deg, rgba(129,230,217,1) 0%, rgba(79,209,197,1) 100%);
  border: none;
  border-radius: 1000px;
  box-shadow: 12px 12px 24px rgba(79,209,197,.64);
  transition: all 0.3s ease-in-out 0s;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 10px;
  }

  .Landing_btn__2GIZO::before {
content: '';
  border-radius: 1000px;
  min-width: calc(300px + 12px);
  min-height: calc(60px + 12px);
  border: 6px solid #00FFCB;
  box-shadow: 0 0 60px rgba(0,255,203,.64);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all .3s ease-in-out 0s;
}

.Landing_btn__2GIZO:hover, .Landing_btn__2GIZO:focus {
  color: #313133;
  transform: translateY(-6px);
}

.Landing_btn__2GIZO:hover::before, .Landing_btn__2GIZO:focus::before {
  opacity: 1;
}

.Landing_btn__2GIZO::after {
  content: '';
  width: 30px; height: 30px;
  border-radius: 100%;
  border: 6px solid #00FFCB;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: Landing_ring__9VzP0 1.5s infinite;
}

.Landing_btn__2GIZO:hover::after, .Landing_btn__2GIZO:focus::after {
  animation: none;
  display: none;
}

@keyframes Landing_ring__9VzP0 {
  0% {
    width: 30px;
    height: 30px;
    opacity: 1;
  }
  100% {
    width: 300px;
    height: 300px;
    opacity: 0;
  }
}
